//project mixins

.hide{
  display:none;
}

.block{
  width:100%;
  float:left;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.transition(@val){
  -moz-transition: all @val ease 0s;
  -webkit-transition: all @val ease 0s;
  -o-transition: all @val ease 0s;
  transition: all @val ease 0s;
}

